<template>
  <div class="form flex flex-col text-sm">
    <div class="flex flex-col mb-2">
      <template v-if="!loading && cansave">
        <button class="w-full border shadow" @click="$emit('save-map')">
          Save
        </button>
      </template>
      <template v-else>
        <span class="flex text-xs">XML</span>
        <input
          :disabled="loading"
          class="flex text-xs"
          @change="getFile"
          type="file"
        />
      </template>
    </div>
    <div class="flex flex-col">
      <span class="flex text-xs">Image</span>
      <input class="flex text-xs" @change="getImage" type="file" />
    </div>
  </div>
</template>

<script>
import { mapFromXMLFile } from "../utils/routeparser";

export default {
  name: "LoadRoute",
  data: () => ({
    loading: false
  }),
  props: {
    cansave: Boolean
  },
  methods: {
    loadingStatus(loading) {
      if (loading === this.loading) {
        return;
      }
      this.loading = loading;
      this.$emit("loading-status", loading);
    },
    getImage(event) {
      if (event.target.files[0].type !== "image/png") {
        return;
      }
      let mapImageURL = URL.createObjectURL(event.target.files[0]);
      if (mapImageURL) {
        this.$emit("map-image-change", mapImageURL);
      }
    },
    getFile(event) {
      this.loadingStatus(true);
      let selectedFile = event.target.files[0];
      let reader = new FileReader();

      reader.onload = async e => {
        this.loadingStatus(false);
        this.$emit(
          "map-xml-loaded",
          await mapFromXMLFile(e.target.result, selectedFile.name)
        );
      };
      reader.readAsText(selectedFile);
    }
  }
};
</script>

